import PropTypes from "prop-types";

import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Icon from "@mui/material/Icon";
import Stack from "@mui/material/Stack";
import MKBox from "components/MKBox";
import MKTypography from "components/MKTypography";

function MemberCard({ image, name, position, publications }) {
  const renderPublications = publications.map(({ title, description }) => (
    <MKBox key={title} display="flex" alignItems="center" p={1} sx={{ pb: 3 }}>
      <MKBox
        width="2rem"
        height="2rem"
        variant="gradient"
        bgColor="info"
        color="white"
        coloredShadow="info"
        display="flex"
        alignItems="center"
        justifyContent="center"
        borderRadius="lg"
        sx={{ flexShrink: 0 }}
      >
        <Icon fontSize="small">article</Icon>
      </MKBox>
      <MKTypography variant="body2" color="text" pl={2}>
        <strong>{title}</strong>
        <br />
        {description}
      </MKTypography>
    </MKBox>
  ));

  return (
    <MKBox component="section" py={3}>
      <Container>
        <Grid container alignItems="flex-start" justifyContent="space-between">
          {/* ---    () --- */}
          <Grid item xs={12} md={4} sx={{ textAlign: "center" }}>
            <MKBox
              component="img"
              src={image}
              alt={name}
              width="80%" //       .
              sx={{ mb: 2, borderRadius: 2, mx: "auto" }}
            />
            <MKTypography variant="h4" my={1} sx={{ pb: 2 }}>
              {" "}
              {name}{" "}
            </MKTypography>
            <MKBox sx={{ display: "inline-block", textAlign: "left" }}>
              <MKTypography variant="body2" color="text" mb={1}>
                <strong>{position.course}</strong>
              </MKTypography>
              <MKTypography variant="body2" color="text" mb={1}>
                <strong>{position.interest}</strong>
              </MKTypography>
            </MKBox>
          </Grid>

          {/* ---    () --- */}
          {/* ======== []   (padding-left)   . ======== */}
          <Grid
            item
            xs={12}
            lg={6}
            sx={{
              mt: { xs: 6, lg: 0 },
              maxHeight: "500px", //        .
              overflowY: "auto", //  maxHeight     .
            }}
          >
            <Stack>{renderPublications}</Stack>
          </Grid>
        </Grid>
      </Container>
    </MKBox>
  );
}

// props      .
MemberCard.propTypes = {
  image: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  position: PropTypes.shape({
    course: PropTypes.string.isRequired,
    interest: PropTypes.string.isRequired,
  }).isRequired,
  publications: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      description: PropTypes.string.isRequired,
    })
  ).isRequired,
};

export default MemberCard;
